import * as _swal from 'sweetalert';
import { ClothesService } from './clothes.service';
var swal = _swal;
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
// import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
var AppComponent = /** @class */ (function () {
    function AppComponent(spinnerService, paneldetail) {
        this.spinnerService = spinnerService;
        this.paneldetail = paneldetail;
        this.selectedfile = null;
        this.nameitem = '';
        this.colors = [];
        this.login = false;
        this.append = "data:image/jpeg;base64,";
        this.ccc = "";
        this.clothObject = {
            "clothImage": this.append + "",
            "clothName": "",
            "color": "",
            "shade": "",
            "fitting": "",
            "pattern": "",
            "patternSpecial": "",
            "patternColor": "",
            "colorTag": "",
            "type": ""
        };
        this.userObject = {
            "username": "",
            "password": ""
        };
        this.getClothdetails();
        // this.go();
        this.naam = localStorage.getItem('Username');
        if (localStorage.getItem('accesstoken')) {
            this.login = true;
            this.naam = localStorage.getItem('Username');
        }
        else {
            this.login = false;
            this.naam = localStorage.getItem('Username');
        }
    }
    AppComponent.prototype.getClothdetails = function () {
        var _this = this;
        // this.navCtrl.push(QuestionsPage);
        this.paneldetail.getdetails().subscribe(function (res) {
            _this.clothtypes = res.data.clothTypes;
            //  this.colors=res.data.colors;
            //  console.log('dasdasdasdadasdas',this.colors);
            _this.shades = res.data.shades;
            _this.patterns = res.data.patterns;
            _this.patternspecial = res.data.patternSpecials;
        });
    };
    // go(){
    //   this.paneldetail.postData().subscribe((res)=>{
    //     xml2js.parseString(res);
    //   console.log(res);
    //   });
    // }
    AppComponent.prototype.ngOnInit = function () {
        this.backgroundColor = '#fff';
        this.fontColor = '#222';
        this.linkColor = '#4b4fce';
    };
    /**
     * Set color from color picker
     * @param {string} type
     * @param {string} color
     */
    AppComponent.prototype.setColor = function (type, color) {
        switch (type) {
            case 'background':
                this.backgroundColor = color;
                break;
            case 'font':
                this.fontColor = color;
                break;
            case 'link':
                this.linkColor = color;
                break;
            default:
                break;
        }
    };
    AppComponent.prototype.preview = function (files) {
        var _this = this;
        if (files.length === 0)
            return;
        this.nameitem = files[0].name.split(".")[0];
        console.log(this.nameitem);
        var mimeType = files[0].type;
        if (mimeType.match(/image\/*/) == null) {
            this.message = "Only images are supported.";
            return;
        }
        var reader = new FileReader();
        this.imagePath = files;
        reader.readAsDataURL(files[0]);
        reader.onload = function (_event) {
            _this.imgURL = reader.result;
            _this.encoded = _this.imgURL.toString();
            // console.log(this.encoded);
        };
    };
    AppComponent.prototype.done = function () {
        var _this = this;
        this.spinnerService.show();
        this.clothObject.colorTag = localStorage.getItem('Ctag');
        console.log(this.colors.hex);
        this.clothObject.color = localStorage.getItem('Cname');
        this.clothObject.clothImage = this.encoded;
        this.clothObject.clothName = this.nameitem;
        this.clothObject.patternColor = this.linkColor;
        // console.log(this.clothObject);
        this.paneldetail.postdeatil(this.clothObject).subscribe(function (res) {
            console.log(res);
            if (res.status == 0) {
                _this.failed();
                _this.spinnerService.hide();
            }
            else {
                // console.log(this.spinnerService.show())
                // this.spinnerService.show();
                _this.allgood();
                _this.clothObject.clothImage = '';
                _this.clothObject.clothName = '';
                _this.clothObject.color = '';
                // this.clothObject.patternColor = '';
                _this.clothObject.patternSpecial = '';
                _this.clothObject.shade = '';
                _this.clothObject.type = '';
                _this.clothObject.fitting = '';
                _this.clothObject.pattern = '';
                _this.backgroundColor = '';
                _this.linkColor = '';
                _this.nameitem = '';
                _this.encoded = '';
                _this.imgURL = '';
                _this.clothObject.colorTag = '';
                localStorage.removeItem('Ctag');
                localStorage.removeItem('Cname');
                _this.spinnerService.hide();
            }
            // console.log(this.ccc)
        });
    };
    AppComponent.prototype.allgood = function () {
        swal("Successful", "Record has been saved", "success");
    };
    AppComponent.prototype.failed = function () {
        swal("Oopss", "Something went Wrong", "error");
    };
    AppComponent.prototype.loginFunction = function () {
        var _this = this;
        console.log('object', this.userObject);
        this.paneldetail.addsignin(this.userObject).subscribe(function (res) {
            // console.log(res)
            if (res.status == '0') {
                swal("Oopss", res.message, "error");
                // this.loginfailToast(res.message);
            }
            else if (res.userExist.active == false) {
                swal("Oopss", res.message, "error");
                // } else if (res.userExist.isFirstlogin == false) {
                //   this.signinNav();
                //   this.presentToast();
                //   this.userObject.username = '';
                //   this.userObject.password = '';
                //   localStorage.setItem('accesstoken', res.token.access_token);
                //   localStorage.setItem('Refreshtoken', res.token.refresh_token);
                //   localStorage.setItem('tokenExpiry', res.token.expires_in);
                //   localStorage.setItem('UserID', res.userExist.id);
                //   localStorage.setItem('Username', res.userExist.fullName);
                //   localStorage.setItem('temp', res.userExist.settings.temp);
                //   localStorage.setItem('email', res.userExist.email);
                //   localStorage.setItem('phone',res.userExist.number)
                //   localStorage.setItem('FL',res.userExist.isFirstlogin)
            }
            else {
                _this.login = true;
                // swal("Successful",res.message, "success");
                _this.userObject.username = '';
                _this.userObject.password = '';
                localStorage.setItem('accesstoken', res.token.access_token);
                localStorage.setItem('Username', res.userExist.fullName);
                _this.naam = localStorage.getItem('Username');
                // this.events.publish('setimagename');
                // localStorage.setItem('Refreshtoken', res.token.refresh_token);
                // localStorage.setItem('tokenExpiry', res.token.expires_in);
                // localStorage.setItem('UserID', res.userExist.id);
                // localStorage.setItem('Username', res.userExist.fullName);
                // localStorage.setItem('temp', res.userExist.settings.temp);
                // localStorage.setItem('email', res.userExist.email);
                // localStorage.setItem('phone',res.userExist.number);
                // localStorage.setItem('FL',res.userExist.isFirstlogin)
            }
        });
    };
    AppComponent.prototype.logout = function () {
        localStorage.clear();
        this.login = false;
    };
    return AppComponent;
}());
export { AppComponent };



