import { HttpClient } from '@angular/common/http';
// mport { ConfigProvider } from '../config/config';
import 'rxjs/add/operator/map';
import { HttpHeaders } from '@angular/common/http';
var httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};
var ClothesService = /** @class */ (function () {
    function ClothesService(http) {
        this.http = http;
        // baseURL:any="http://dressmepro-server.kq39epmump.us-east-2.elasticbeanstalk.com";
        // baseURL:any="https://368f3cd8.ngrok.io"
        this.baseURL = "https://api.dressmepro.com";
    }
    ClothesService.prototype.getdetails = function () {
        // const httpOptions = {
        //   headers: new HttpHeaders({ 'Content-Type': 'application/json' , 'Authorization' : 'Bearer ' + this.token  })
        // };
        return this.http.get(this.baseURL + '/cloth/getPanelData')
            .map(function (res) { return res; });
    };
    ClothesService.prototype.postdeatil = function (user) {
        // const httpOptions = {
        //   headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization' : 'Bearer ' + this.token  })
        // };
        return this.http.post(this.baseURL + '/cloth/add', user, httpOptions)
            .map(function (res) { return res; });
    };
    ClothesService.prototype.addsignin = function (user) {
        return this.http.post(this.baseURL + '/user/login', user, httpOptions)
            .map(function (res) { return res; });
    };
    return ClothesService;
}());
export { ClothesService };



